<template>
  <div class="contanier">
    <div class="row" v-if="showStatment" >
      <DeliveryBalance-Table :idDelivery="idDelivery.id" :nameDelivery="idDelivery.name" class="flex md12 sm12 xs12"/>
    </div>
    <div class="row" v-if="!showStatment" style="justify-content: center;" >
      <va-card :title="$t('tables.labelDeliveryBalance')" class="flex md8 sm12 xs12">
        <form @submit.prevent="okCreatefun()">
          <div class="row flex-center">
            <div class="flex md4 sm12 xs12 px-1">
              <label class="input-label"> {{$t('placeholder.deliveryName') +' *'}} </label>

              <va-select
                :label="$t('placeholder.deliveryName')"
                v-model="fKEmp.value"
                required
                searchable
                textBy="name"
                keyBy="id"
                :options="fKEmp.options"
              />
            </div>
          </div>

          <div class="d-flex justify--center mt-3">
            <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t('modal.ok') }} </va-button>
          </div>
        </form>
      </va-card>
    </div>
  </div>
</template>

<script>
import DeliveryBalanceTable from './DeliveryBalance/DeliveryBalanceTable'
import employeesService from '../../../services/BasicData/employeesService'

export default {
  name: 'DeliveryBalance',
  components: {
    DeliveryBalanceTable,
  },
  data () {
    return {
      fKEmp: {
        value: '',
        options: [],
      },
      idDelivery: 0,
      showStatment: false,
      stopBtn: false,
    }
  },
  created () {
    this.getCustomer()
  },
  methods: {
    getCustomer () {
      employeesService.getAll()
        .then(response => {
          console.log(';ddd')

          this.fKEmp.options = response.data.filter(function (x) { return x.is_Delivery === true })
        },
        err => {
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    okCreatefun () {
      this.stopBtn = true
      if ((this.fKEmp.value !== null && this.fKEmp.value !== undefined && this.fKEmp.value !== '')) {
        this.idDelivery = this.fKEmp.value

        this.showStatment = !this.showStatment
      } else {
        this.stopBtn = false
        this.showToast(this.$t('generic.errorChooseEmp'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
    },
  },
}

</script>

<style lang="scss">
  .row-equal .flex {
    .va-card {
      height: 100%;
    }
  }

  .dashboard {
    .va-card {
      margin-bottom: 0 !important;
    }
  }

</style>
